import React from 'react';
import { Link } from 'gatsby';
import styled from 'styled-components';
import logoSrc from '../assets/images/logo.png';

const Nav = () => (
  <NavStyles>
    <ul>
      <li>
        <Link to="/">
          <Image src={logoSrc} alt="logo" />
        </Link>
      </li>
      <li>
        <Link to="/">Home</Link>
      </li>
      <li>
        <Link to="/faq">FAQ</Link>
      </li>
      <li>
        <Link to="/order">Book</Link>
      </li>
    </ul>
  </NavStyles>
);
export default Nav;

const Image = styled.img`
  width: 200px;
  height: 200px;
`;

const NavStyles = styled.nav`
  margin-bottom: 3rem;
  ul {
    margin: 0;
    padding: 0;
    text-align: center;
    list-style: none;
    margin-top: -6rem;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 2rem;
    align-items: center;
  }
  li {
    --rotate: -2deg;
    transform: rotate(var(--rotate));
    order: 1;
    &:nth-child(1) {
      --rotate: 1deg;
    }
    &:nth-child(2) {
      --rotate: -2.5deg;
    }
    &:nth-child(4) {
      --rotate: 2.5deg;
    }
    &:hover {
      --rotate: 3deg;
    }
  }
  a {
    font-size: 3rem;
    text-decoration: none;
    &:hover {
      color: var(--red);
    }
    &[aria-current='page'] {
      color: var(--red);
    }
  }
`;
